import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { AppointmentEventsComponent } from './appointment-events.component';


const routes: Routes = [{
  path: '', component: AppointmentEventsComponent,
  children: [
    { path: 'event-types', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/appointment/appointment-events/appointment-event-types/appointment-event-types.module').then(m => m.AppointmentEventTypesModule) },
    { path: 'scheduled-events', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/appointment/appointment-events/appointment-scheduled-event/appointment-scheduled-event.module').then(m => m.AppointmentScheduledEventModule) },
    { path: '', pathMatch : 'full', redirectTo: 'event-types' },
  ]
}];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AppointmentEventsRoutingModule { }
